@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

* {
  font-family: 'Montserrat', sans-serif;
}

.App {
  text-align: center;
  font-size: calc(4px + 2vmin);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
}

.App div {
    margin: 2vh;
}

.productList {
  flex-direction: row;
  flex-wrap: wrap;
}

.product div {
  margin: 0.75vh;
}

.product {
  padding: 2vh;
  margin-left: 1vh;
  margin-right: 1vh;
  margin-bottom: 2vh;
  outline: 2px solid black;
}

.removeButton {
  background: red;
  color: white;
  margin-bottom: auto;
  margin-left: auto;
  border-radius: 4px;
  text-align: center;
  border:none;
  height: 30px;
  width: auto;
}

.cancelButton {
  background: red;
  color: white;
  margin-bottom: auto;
  margin-left: auto;
  border-radius: 4px;
  text-align: center;
  border:none;
  height: 30px;
  width: auto;
}

.removeButton2 {
  background: rgb(66, 184, 66);
  color: white;
  top: 0;
  right: 0;
  border-radius: 4px;
  text-align: center;
  border:none;
  height: 30px;
  width: auto;
}

.cartProduct {
  display: flex;
  width: 22vw;
  flex-direction: row;
  background: black;
  margin-bottom: 1vh;
}

.cartProduct .price {
  right: 0;
}

.cartProduct img {
  height: 5vh;
  width: auto;
}

.shoppingButton {
  position: absolute;
  right: 1vw;
  font-size: calc(8px + 1vmin);
}

.shoppingCartButtons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.shoppingCart {
  background: rgb(35,35,35);
  color: white;
  width: 25vw;
  height: auto;
  margin: auto;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border-radius: 6px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.buttons {
  flex-direction: row;
}

.buttons form input {
  width: 7vw;
}

.cartAmount {
  width: 2vw !important;
}

.desc {
  font-size: calc(4px + 1.5vmin);
}

form label {
  font-size: calc(4px + 1.25vmin);
}

form input {
  width: 100%;
}

.price {
  color: red;
}


div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

input {
  padding: 8px;
  margin: 4px;
}

button {
  padding: 8px;
  margin: 4px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: white;
}

img {
  height: 20vh;
}

.App-link {
  color: #61dafb;
}

.image-data {
  border: 1px solid black;
  padding: 4px;
}

.link-data {
  position: relative;
  border: 1px solid black;
  padding: 4px;
  padding-top: 8px;
  width: 600px;
}

.link-data .embed {
  position: relative;
  width:400;
  height: auto;
}

.link-data .likes {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-right: 16px;
  margin-bottom: 16px;
  text-align: right;
  user-select: none;
}

.link-data .id {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 16px;
  margin-top: 16px;
  text-align: left;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

table {
  width: 150%;
}

table th {
  background: lightgray;
  padding: 6px;
  border: 1px solid black;
}

table td {
  background: lightblue;
  padding: 6px;
  border: 1px solid black;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
